* {
  box-sizing: border-box;
}

body {
  margin: 0px;
  padding: 0px;
  background-color: white;
  height: 100%;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: grey;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.loadering {
  display: flex;
  margin-bottom: 80px;
  margin-right: -60px;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  width: 80%;
  display: flex;
  padding-top: 70px;
}

.movies .movie {
  width: 45%;
  background-color: black;
  color: #d2d2e2;
  margin-bottom: 70px;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  position: relative;
  border-radius: 5px;
}

.movie img {
  position: relative;
  top: -50px;
  width: 100%;
  max-width: 150px;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(222, 222, 230, 0.596),
    0 8px 16px -8px rgba(209, 200, 200, 0.3),
    0 -6px 16px -6px rgba(230, 225, 225, 0.767);
}

.movie .movie__tilte,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 22px;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 5px;
  display: flex;
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}
